import React, { useContext, useEffect } from "react";
import { RootContextProvider, RootContext } from "./src/contexts/rootContext";
export const wrapRootElement = ({ element }) => (
  <RootContextProvider>
    <Container>{element}</Container>
  </RootContextProvider>
);

const Container = React.memo(({ children }) => {
  const { fetchProfile } = useContext(RootContext);
  useEffect(() => {
    fetchProfile();
  }, [fetchProfile]);
  return children;
});
