import React, {
  createContext,
  useMemo,
  useReducer,
  Dispatch,
  useCallback,
} from "react";
import axios from "axios";

type State = {
  profile: Profile | null;
};

type RootContext = {
  state: State;
  setState: Dispatch<State>;
  fetchProfile: () => void;
};

const initState: State = {
  profile: null,
};

const context: RootContext = {
  state: initState,
  setState: () => {},
  fetchProfile: () => {},
};

const reducer = (prevState: State, nextState: State) => ({
  ...prevState,
  ...nextState,
});

export const RootContext = createContext<RootContext>(context);

export const RootContextProvider = React.memo(({ children }) => {
  const [state, setState] = useReducer(reducer, initState);
  const fetchProfile = useCallback(async () => {
    try {
      const result = await axios.get(
        "https://nayunhwan.github.io/whoami/me.json"
      );
      setState({
        profile: result.data,
      });
    } catch (err) {
      throw new Error(err);
    }
  }, []);

  return (
    <RootContext.Provider
      value={useMemo(
        () => ({
          state,
          setState,
          fetchProfile,
        }),
        [state, setState, fetchProfile]
      )}
    >
      {children}
    </RootContext.Provider>
  );
});
